import React, { useEffect, useMemo, FC } from 'react';
import ReactDOM from 'react-dom';
import { Transition } from 'react-transition-group/esm';

import { withoutSSR } from '../../common/utils/without-ssr';

import './popup.less';

const APPEARANCE_DURATION = 5_000;

export interface PopupProps {
  isVisible: boolean;
  onClose: () => void;
}

function useContainer() {
  const element = useMemo(() => document.createElement('div'), []);
  useEffect(() => {
    document.body.appendChild(element);
    return () => {
      document.body.removeChild(element);
    };
  }, []);
  return element;
}

const PopupBase: FC<PopupProps> = ({ isVisible, onClose, children }) => {
  const container = useContainer();

  return ReactDOM.createPortal(
    <Transition
      in={isVisible}
      onEntered={onClose}
      timeout={APPEARANCE_DURATION}
      unmountOnExit
      mountOnEnter
      exit={false}
    >
      {children}
    </Transition>,
    container
  );
};

export const Popup = withoutSSR(PopupBase);
