type ErrorListener = (e?: Error) => void;

class ErrorEmitter {
  private listeners: Array<ErrorListener> = [];

  emit(error?: any) {
    this.listeners.forEach((f) => f(error));
  }

  addErrorListener(listener: ErrorListener) {
    if (this.listeners.length > 0) {
      // TODO: in case of widget we have a few TipserElementsProviders mounted so this condition assure that only one of them shows an error message. it has to be change asap. https://tipser.atlassian.net/browse/T3-4450
      console.warn(
        "Currently ErrorEmitter functionality is limited and your listener won' be called. See: https://tipser.atlassian.net/browse/T3-4450"
      );
      return;
    }
    this.listeners.push(listener);
  }

  removeErrorListener(listener: ErrorListener) {
    this.listeners = this.listeners.filter((f) => f !== listener);
  }
}

export const errorEmitter = new ErrorEmitter();
